import React from "react";
import {useState} from "react"
const Nav = ()=>{
    const [toggle, setToggle]=useState(false)
    const menu=()=>{
        setToggle(!toggle)
    }
    return(
        <div>

            <ul>
                <li>
                    <button>Menu</button>
                    {toggle&&(
                        <ul>
                            <li>Fish</li>
                        </ul>
                    )}
                </li>

            </ul>
        </div>
    )
}

export default Nav